nav>a,
.btn {
    color: #2e3440;
    background-color: #eceff4;
    text-decoration: none;
}

.btn.all {
    background-color: #d8dee9;
}

nav>a:hover,
.btn:hover {
    background-color: #c5cedd;
    text-decoration: underline;
}

nav>.current,
.btn.active {
    background-color: #cfe3e2;
    text-decoration: underline;
}

.codeblock {
    background-color: #e5e9f0;
}

html {
    color: #3b4252;
}

body {
    background-color: white;
}

nav {
    background-color: #4c566a;
}

footer {
    background-color: #4c566a;
    color: white;
}

footer>a {
    color: white;
    text-decoration: none;
}

code {
    background-color: #e5e9f0;
}

a>i {
    color: #63adc2;
}

a>i:hover {
    color: #e7c175;
}